/* src/globalStyles.css */

:root {
  --primary-background: #f0f0f5;
  --hover-background: #f0f0f5c2;
  --primary-button-background: rgb(172, 72, 99); /* Dusty Rose */
  --secondary-button-background: rgb(72, 177, 93); /* Pale Mauve (light green in provided RGB) */
  --button-hover-dusty-rose: #c2a4ac;
  --button-hover-pale-mauve: rgb(172, 228, 176); /* Pale Mauve hover (light green hover) */
  --heading-text-color: #D2B4BC; /* Dusty Rose */
  --primary-text-color: #424242; /* Dark Gray */
  --social-links-color: #757575; /* Medium Gray */
  --social-links-hover-color: #FAEBD7; /* Antique White */

  /* New color variables added from about.css */
  --secondary-text-color: #555;
  --light-shadow-color: #e3e3e3;
  --white-shadow-color: #ffffff;
  --gradient-light-grey: #d0d0d0;
  --paragraph-text-color: #606060;
  --off-white-background: #f8f8ff;
  --marquee-text-color: #444;
  --lighter-shadow-color: #dcdbdb;
  --skill-tag-hover-background: #f3f3f3;
  --skill-tag-active-background: #e8e8ee;

  /* New color variables added from aboutcertificates.css */
  --slide-arrow-background: var(--heading-text-color); /* Using heading text color which is Dusty Rose */
  --slide-arrow-text-color: white;
  --slide-arrow-hover-background: var(--button-hover-dusty-rose); /* Using button hover dusty rose */

  /* New color variables added from certificates.css */
  --certificate-dashed-border-color: #e2d8e8;
  --certificate-highlight-color: #aa7ab7;
  /* New color variables added from contactForm.css */
  --form-background-color: var(--off-white-background); /* Using off-white-background for form background */
  --form-box-shadow-light: #e0e0e0;
  --form-box-shadow-white: #ffffff;
  --form-heading-color: var(--heading-text-color); /* Using heading text color for form heading */
  --form-label-color: var(--secondary-text-color); /* Using secondary text color for form labels */
  --form-input-text-color: var(--primary-text-color); /* Using primary text color for form input text */
  --form-field-background: var(--primary-background); /* Using primary background for form fields */
  --form-field-border-color: #e8e8e8;
  --form-field-focus-border-color: var(--heading-text-color); /* Using heading text color for focused form field border */
  --button-solid-background: var(--heading-text-color); /* Using heading text color for solid button background */
  --button-text-color: white;
  --button-hover-solid-background: var(--button-hover-dusty-rose); /* Using button hover dusty rose for button hover */
  --button-focus-shadow-color: var(--heading-text-color); /* Using heading text color for button focus shadow */

    /* New color variables added from hero.css */
    --hero-background: var(--primary-background); /* Hero background same as primary background */
    --hero-hover-background: var(--hover-background); /* Hero hover background same as hover background */
    --hero-box-shadow-light: #e0e0e0;
    --hero-box-shadow-white: #ffffff;
    --hero-hover-box-shadow-light: #d0d0d0;

  /* New color variables added from portfolio.css */
  --portfolio-background: var(--primary-background); /* Portfolio section background */
  --portfolio-hover-background: var(--hover-background); /* Portfolio section hover background */
  --portfolio-box-shadow-light: #e0e0e0;
  --portfolio-box-shadow-white: #ffffff;
  --portfolio-hover-box-shadow-light: #d0d0d0;
  --portfolio-section-text-color: var(--primary-text-color); /* General text color for portfolio section */
  --portfolio-heading-color: var(--heading-text-color); /* Heading color for portfolio section */
  --portfolio-paragraph-text-color: var(--paragraph-text-color); /* Paragraph text color */
  --portfolio-list-item-color: var(--secondary-text-color); /* Color for list items in portfolio */
  --accordion-background: var(--off-white-background); /* Accordion background color */
  --accordion-box-shadow-light: var(--form-box-shadow-light); /* Using form box shadow light for accordion */
  --accordion-box-shadow-white: var(--form-box-shadow-white); /* Using form box shadow white for accordion */
  --accordion-border-color: #f0f0f5; /* Border color for accordion items */
  --accordion-summary-color: var(--heading-text-color); /* Summary text color in accordion */
  --accordion-summary-hover-background: var(--hover-background); /* Hover background for accordion summary */
  --accordion-toggle-icon-color: var(--heading-text-color); /* Toggle icon color in accordion */
  --accordion-panel-background: var(--off-white-background); /* Background color for accordion panel */
  --accordion-panel-text-color: var(--paragraph-text-color); /* Text color in accordion panel */
  --project-image-box-shadow-light: var(--form-box-shadow-light); /* Shadow for project images */
  --project-image-box-shadow-white: var(--form-box-shadow-white); /* White shadow for project images */
  --project-link-button-background: var(--heading-text-color); /* Background for project link buttons */
  --project-link-button-text-color: var(--button-text-color); /* Text color for project link buttons */
  --project-link-button-hover-background: var(--button-hover-dusty-rose); /* Hover background for project link buttons */
  --portfolio-highlight-color: var(--heading-text-color); /* Highlight color in portfolio section */
/* New color variables added from whychooseme.css */
--why-choose-me-background: var(--primary-background); /* Why Choose Me section background */
--why-choose-me-box-shadow-light: var(--light-shadow-color); /* Shadows for Why Choose Me section */
--why-choose-me-box-shadow-white: var(--white-shadow-color);
--why-choose-me-text-color: var(--primary-text-color); /* Text color for Why Choose Me section */
--why-choose-me-heading-color: var(--heading-text-color); /* Heading color for Why Choose Me section */
--reason-item-background: var(--off-white-background); /* Background for reason items */
--reason-item-box-shadow-light: var(--light-shadow-color); /* Shadows for reason items */
--reason-item-box-shadow-white: var(--white-shadow-color);
--reason-item-hover-box-shadow-light: var(--gradient-light-grey); /* Hover shadow for reason items */
--reason-item-paragraph-color: var(--secondary-text-color); /* Paragraph color for reason items */
  /* New color variables added from footer.css */
  --footer-background: var(--off-white-background);
  --footer-text-color: var(--secondary-text-color);
  --footer-box-shadow-light: var(--form-box-shadow-light); /* Reusing form box shadow light */
  --footer-box-shadow-white: var(--form-box-shadow-white); /* Reusing form box shadow white */
  --footer-link-hover-color: var(--heading-text-color); /* Reusing heading text color for link hover */
/* New color variables added from navbar.css */
--navbar-background: var(--primary-background);
--navbar-box-shadow-light: var(--light-shadow-color);
--navbar-box-shadow-white: var(--white-shadow-color);
--navbar-logo-color: var(--heading-text-color);
--navbar-logo-text-shadow: rgba(230, 230, 230, 0.7);
--navbar-links-color: var(--primary-text-color);
--navbar-links-hover-color: var(--heading-text-color);
--navbar-links-hover-box-shadow-light: var(--light-shadow-color);
--navbar-links-hover-box-shadow-white: var(--white-shadow-color);
--navbar-toggle-color: var(--heading-text-color);
--navbar-mobile-menu-background: #f8f8ffe0; /* Semi-transparent off-white for mobile menu */
--navbar-scrolled-background: var(--primary-background);
--navbar-scrolled-box-shadow: rgba(120, 120, 130, 0.08);
--navbar-scrolled-links-color: var(--paragraph-text-color); /* Using paragraph text color for scrolled links */
  /* New color variables added from app.css */
  --body-text-color: #333;
  --link-hover-color: var(--heading-text-color); /* Reusing heading text color for link hover */
  --next-arrow-background: var(--heading-text-color); /* Reusing heading text color for next arrow background */
  --next-arrow-text-color: var(--button-text-color); /* Reusing button text color for next arrow text */
  --next-arrow-hover-background: #C2A4AC; /* Slightly darker dusty rose for hover */
  --next-arrow-hover-box-shadow: rgba(0, 0, 0, 0.2); /* Shadow for next arrow hover */
}